  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  background: #F0F0F0;
  min-width: 700px;
  width: 100%;
}
#app {
  font-family: 'Nunito Sans',  'Quattrocento',  'Concourse T3', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* font-size: 1.5em; */
  line-height: 1.3em;
  color: #3c4e60;
  width: 100%;
  min-width: 700px !important;
  height: 100%;
  display: flex;
  flex-direction: column;
}
#top-pane {
  position: relative;
  width: 100%;
  flex-shrink: 0;
  margin: auto;
  height: 100%;
  min-width: 700px !important;
  display: flex;
  flex-direction: row;
  /* background: url(http://performancetheory.site/assets/bg.jpg) lightseagreen; */
  /* background: url(https://jooinn.com/images/black-paper-texture-5.jpg); */
  /* background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%); */
  /* background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%); */
  /* background-image: linear-gradient(to top, #96fbc4 0%, #f9f586 100%); */
  /* background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%); */
  /* background-image: linear-gradient(to top, #00c6fb 0%, lightseagreen 100%); */
  background-image: linear-gradient(to top, #0c7218, #648f40, #9ead6f, #d1d1a5); /*, #f6f1e1); */
  /*, #f6f1e1);*/
  /* background: url(https://images.pexels.com/photos/246212/pexels-photo-246212.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260); */
  /* background: url(https://stmed.net/sites/default/files/tape-tension-pattern-wallpapers-25320-6152058.jpg); */
}

.graph-node {
  cursor: pointer;
}

#graph {
  min-width: 500px;
  width: 70%;
  flex-grow: 1;
  border-radius: 0 0 1em 0;
  /* background: black; */
}

#graph svg {
  margin: 0
}
#preview, #connections {
  z-index: 10;
  width: 330px;
  overflow-y: auto;
  padding: 10px;
  text-align: center;
  background: rgba(123, 209, 113, 0.7);
  border-radius: 4px;
  display: flex;
  margin-bottom: 20px;
  flex-direction: column;
  color: #333;
}

#preview {
  cursor: pointer;
  flex-shrink: 0;
}

#preview:hover {
  background: rgba(123, 209, 113, 0.9);
}

#title-panel, #preview-panel {
  position: absolute;
  z-index: 10;
  top: 5%;
  right: 20px;
  width: 350px;
  display: flex;
  flex-direction: column;
}

#title-div, #instructions {
  background: #7AD171;
