}

#instructions {
  font-size: calc(0.9em + .5vh);
  line-height: 1.3em;
  padding-bottom: 40px;
  text-align: left;
}

#instructions ul {
  list-style-type: none;
  text-align: center;
  margin: 0;
  padding: 0;
}

#preview img {
  margin: auto;
  border-radius: 2px;
  max-height: 200px;
  max-width: 300px;
  margin-bottom: 10px;
}

#preview .typetag {
  margin: auto;
  text-align: center;
  padding: 0.2em 1em 0.2em 1em;
  border-radius: 10px;
  background: #0c7218;
  color: #F6EDD9;
  font-weight: bold;
  /* box-shadow: 2px 2px rgba(0, 74, 28, 0.2); */
  margin-bottom: 20px;
  margin-top: 20px;
}


main {
  padding-top: 5em;
  padding-right: 200px;
  padding-left: 5em;
  padding-bottom: 5em;
  background: rgba(240, 240, 240, 0.9);
  /* overflow-y: auto; */
  min-width: 200px;
  width: 100%;
  box-sizing:border-box;
  position: absolute;
  top: 0;
  left: 0;
  overflow-y: scroll;
  height: 100%;
  margin-bottom: 0;
  z-index: 20;
  transition: 0.5s;
  /* flex-grow: 1; */
}

.fade-leave-active {
  transition: 0.3s;
}
.fade-enter-active {
