  transition: 0.4s;
}
.slide-enter {
  opacity: 0.0;
}
.slide-leave-to {
  opacity: 0;
}

svg .not-focused {
  opacity: 0.2;
  transition: 0.5s;
}

svg {
  cursor: url('http://ssl.gstatic.com/ui/v1/icons/mail/images/2/openhand.cur'), move;
  /* background: rgba(0, 0, 0, 0.1); */
}

svg.zooming {
    cursor: url('http://ssl.gstatic.com/ui/v1/icons/mail/images/2/closedhand.cur'), move;
    cursor: -moz-grabbing;
    cursor: -webkit-grabbing;
}

.preview-text p {
  display: block;
  line-height: 1.3em;
  font-size: 1.2em;
  text-align: left;
  width: 300px;
  margin: auto;
  margin-bottom: 10px;
}

h1, h2 {
  line-height: 1.4em;
  margin-left: 10px;
  margin-right: 10px;
}

.preview-text p.read-more {
  font-size: 0.9em;
  color: black;
  font-weight: 100;
  text-align: center;

}

#closeButton {
  z-index: 50;
