}

#preview-panel {
  height: calc(94vh);
  justify-content: space-between;
}

#connections {
  flex-grow: 1;
  height: 100%;
}

#connections ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  /* margin: auto; */
