  margin-right: 5px;
  text-align: center;
  border-radius: 20px;
}

#ackButton {
  background: rgba(0, 0, 0, 0.5);
  color: #f6EDD9;
  padding: 5px;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
  bottom: 5vh;
  right: 5vh;
  position: absolute;
  opacity: 0.7;
  width: 200px;
}

#ackButton:hover {
  opacity: 1;
}

#instructions a {
  color: blue;
}

#instructions a:active {
  color: darkblue;
}
</style>



