    "QueerTheory": {
      "title": "Queer Theory",
      "overview": 'Queer theory is a field of critical discourse that critiques those cultural institutions and practices which enforce normative ways of being, doing, and knowing. It builds upon feminist re-conceptualizations of gender and sexuality by adding the perspective of thinkers who challenge the very viability of, for example, gender identification or sexual orientation. Aligned with the poststructuralist critique of the humanist model of the subject, queer theory offers various understandings of human identity as created performatively, constructed discursively, or else always culturally and historically specifici.e., never age-old, intrinsic, or by nature.\n\nWhile often associated with the LGBT community, queer theory asks questions beyond any one cultural group, and does not limit itself to conversations regarding sexuality and gender. In fact, queer theory joins other theoretical projects in understanding that racial, sexual, national, class, and gender identities all exist at a complex intersection with one another in any given person or culture. As such, queer theory is as much a mode of inquiry and analysis as it is a study of people who call themselves queer. In general, queer theory itself queersin an intellectual and theory senseby taking up various projects of de-legitimizing, troubling, challenging, camping up, or de-familiarizing. It finds objects of analysis across history, but most always seeks to de-stabilize the patriarchal, heteronormative, and racist underpinnings of our society.',
      "image": null,
      "contributor": "Nevarez Encinias",
      "type": "Module",
      "url": null
    },
