    "GloriaAnzaldua": {
      "title": "Gloria Anzalda",
      "overview": "Gloria Anzalda was born in 1942 and died in 2004. She was a scholar of Chicana cultural theory, feminism, and queer theory. She is well known for editing _This Bridge Called My Back: Writings by Radical Women of Color_ with Cherre Moraga and for writing the highly influential _Borderlands/La Frontera: The New Mestiza_. She often wrote many of her texts in Spanglish. In her essay La Prieta, collected in _This Bridge Called My Back_, she argues that the queer can also be considered in terms of race, giving a personal account of the ways in which her skin color affected her life as a queer woman. Anzalda writes, in [my mothers] eyes and in the eyes of others I saw myself reflected as Strange, abnormal, QUEER. I saw no other reflection. In her writing, she also argues that even within the groups with which one self-identifies, one can feel isolated, due to other differences. On this topic, Anzalda says we are the queer groups, the people that dont belong anywhere, not in the dominant world nor completely within our own respective cultures.",
      "image": "https://i.pinimg.com/originals/a0/ce/27/a0ce272717e85dd6e8ffcb0c52e82a5e.jpg", // https://upload.wikimedia.org/wikipedia/commons/1/11/Gloria_Anzaldua.jpg",
      "type": "Author",
      "url": null
    },
    "JoseEstebanMunoz": {
      "title": "Jos Esteban Muoz",
      "overview": "Jos Esteban Muoz was born in 1967 and died in 2013. He was a scholar of performance studies, queer theory, and visual culture, and served as the Chair of the Department of Performance Studies at New York University. Muozs two main works, _Disidentifications: Queers of Color and the Performance of Politics_ and _Cruising Utopia: The Then and There of Queer Futurity_, critique contemporary gay politics and culture, while also offering theories that have deeply influenced the field of queer studies and contributed to contemporary conceptions of queerness, at large.",
      "image": "https://bullybloggers.files.wordpress.com/2013/12/4_13.jpg",
      "type": "Author",
      "url": null
    },
