    "disidentification": {
      "title": "disidentification",
      "overview": "A concept originally developed by Jos Muoz in his 1999 book _Disidentifications: Queers of Color and the Performance of Politics_. The term refers to the survival strategies that the minoritarian subject practices in order to negotiate a phobic majoritarian public sphere. \n\n> Disidentification is about recycling and rethinking encoded meaning. The process of disidentification scrambles and reconstructs the encoded message of a cultural text in a fashion that both exposes the encoded messages universalizing and exclusionary machinations and recircuits its workings to account for, include, and empower minority identities and identifications. Thus, disidentification is a step further than cracking open the code of the majority; it proceeds to use this code as raw material for representing a disempowered politics or positionality that has been rendered unthinkable by the dominant culture (1999:31).",
      "image": null,
      "type": "Keyword",
      "url": null
    },
