    "functionsoflanguage": {
      "title": "functions of language",
      "overview": "Roman Jakobson distinguished six functions of language: the referential (descriptive statements conveying contextual information); the aesthetic/poetic (focuses on the auto-reflection); emotive (self-expression exemplified by interjections, such as Wow!); conative (vocative or imperative addressing of receiver); phatic (language used for the sake of interaction e.g. discussing the weather; checking channel working); metalingual (use of language (code) to discuss itself).",
      "image": null,
      "type": "Keyword",
      "url": null
    },
