    "Postcolonialism": {
      "title": "Postcolonialism",
      "overview": "Postcolonialism, or postcolonial theory, is an intellectual movement that emerged in the mid-20th century. Postcolonial theory undertakes the critical analysis of colonialism and imperialism and examines the cultural, sociopolitical, economic, and psychological effects of colonialism. While postcolonialism is sometimes used to describe the historical period following decolonialization, theorists argue that colonial legacies endure even after independence. As such, postcolonialism can also be seen as a project to reclaim the agency of those subjugated by newer forms of dominationsuch as global empire.",
      "image": null,
      "type": "Module",
      "url": null
    },
