    "Feminisms": {
      "title": "Feminism(s)",
      "overview": "At its core, feminism is the belief in gender equality. The term refers to a broad range of ideologies and movements associated with the advocacy for the establishment of the political, social, and economic rights of women. Feminist theories emerged from womens political movements, and extended feminist ideologies and practices into philosophy and critical theory.\n\nWhile one can find examples of feministor proto-feministtendencies as early as antiquity, the issue of womens rights first became prominent during the French and American revolutions of the late 18th century. Distinct phases of feminist movements are usually referred to as waves. First-wave feminism is often traced back to the beginnings of the suffragette movement of the late 19th century, which focused on womens lack of legal rights. Second-wave feminism emerged in the 1960s, and was primarily concerned with social and economic discrimination against women. Third-wave feminism arose in the 1980s and 1990s largely in response to the lack of racial and economic diversity within second-wave feminism.",
      "image": null,
      "type": "Module",
      "url": null
    },
