    "ecriturefeminine": {
      "title": "ecriture feminine",
      "overview": "(lit. feminine writing; womens writing). First coined by French feminist poet, playwright, and philosopher Helne Cixous in her seminal 1975 essay The Laugh of the Medusa, the term refers to a kind of writing that emblematizes feminine difference and is outside the masculine economy of patriarchal discourse. It expresses what was previously inexpressible; its form emerges out of experimentation and play. Ecriture feminine is a fluid, liberatory practice that embraces fragmented, poetic, and performative writing.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "femininemystique": {
      "title": "feminine mystique",
      "overview": "A concept first named and explored by Betty Friedan in her groundbreaking 1963 book of the same title. The feminine mystique is the false notion that a womans role in society should be limited to that of a wife, mother, and homemaker. According to Friedan, women are socialized to locate their identity and the meaning of their lives through their husbands and children. This process engenders the loss of a womans identity and the displacement of a womans desires for the needs of her spouse and family.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "intersectionality": {
      "title": "intersectionality",
      "overview": "The idea of intersectionality was first introduced by Kimberl Crenshaw in her 1998 discussion of the specific types of discrimination faced by black women. Intersectional theory posits that any one individual belongs to several distinct groups or social categories (e.g. race, class, gender). These interconnected social categorizations create overlapping and interdependent systems of discrimination or disadvantage.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "malegaze": {
      "title": "male gaze",
      "overview": "The male gaze refers to the framing of objects of visual art so that the viewer is situated in the masculine position; objects and phenomena are framed by a male perspective for the viewing pleasure of a heterosexual man. The phrase was coined by Laura Mulvey in her 1975 essay Visual Pleasure and Narrative Cinema.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "masculinist": {
      "title": "masculinist",
      "overview": "Relating to or characteristic of the advocacy of the rights or needs of men and boys; expressing or promoting the opinions or values regarded as typical of men. Some scholars distinguish between masculism and masculinism. Masculism is associated with the gender egalitarianism of the early mens movement, while masculinism refers to patriarchal ideologies.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "thepersonalisthepolitical": {
      "title": "the personal is the political",
      "overview": "This phrase became the underlying political contention and rallying slogan of second-wave feminism of the late 1960s. First popularized by Carol Hanisch in her 1969 essay of the same title, the phrase refers to the interrelation between womens personal experiences and their subordination as women, and further underscores the interconnectedness between personal experience and larger social and political structures and systems.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "phallocentric": {
      "title": "phallocentric",
      "overview": "Centered on the phallus, especially as the symbol of male dominance and authority; man-centered; privileging of the masculine in the construction of meaning.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "womanism": {
      "title": "womanism",
      "overview": "A term proposed by Alice Walker in 1990. An alternative to contemporary feminism, womanism is intended to better address the needs of black women, as well as women of color more broadly.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
