    "deconstruction": {
      "title": "deconstruction",
      "overview": "First introduced by French philosopher Jacques Derrida, deconstruction is a strategy of critical analysis that aims to dismantle binary oppositions, and expose unquestioned assumptions and internal contradictions in philosophical and literary texts.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "deathoftheauthor": {
      "title": "death of the author",
      "overview": "In a 1967 homonymously titled work, Roland Barthes declared the death of the author as a single and authentic source of meaning for a given text, arguing instead for the openness of the text and the birth of the reader as the source of the multiplicity of meanings of the text.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "differance": {
      "title": "diffrance",
      "overview": "A noun coined by Jacques Derrida accentuating the double meaning of the French verb _diffrer_to differ to deffer. Diffrance, or the difference and deferral of meaning, marks the slippage between a work and that to which it refers.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "historicalvsdescriptiveview": {
      "title": "historical vs. descriptive view",
      "overview": "Drawing on Ferdinand de Saussures distinction between historical (diachronic) and descriptive (synchronic) theories of language, poststructuralists generally assert that poststructuralism is historical and structuralism is descriptive.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "biopower": {
      "title": "biopower",
      "overview": "A term coined by Michel Foucault denoting the power of a political entity to control and regulate the lives of its populace.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
    "rhizome": {
      "title": "rhizome",
      "overview": "A philosophical concept proposed by Gilles Deleuze and Flix Guattari. A rhizome is a model and methodology for culture that facilitates multiple, non-hierarchical entry points in a horizontal, de-centered network, which allows immediate connection between any of its points. Rhizomatic mapping resists the organizational structure of the root-tree system, which is hierarchal, ordered, charts causality along chronological lines, and looks for the origin and conclusion of phenomena.",
      "image": null,
      "type": "Keyword",
      "url": null
    },
