    "Phenomenology": {
      "title": "Phenomenology",
      "overview": "Phenomenology is the study of phenomena. It is a form of inquiry into lived experience that is characterized by an openness to everyday experienced meanings as opposed to theoretical ones. As a philosophical movement it was initiated in the early 20th century by German philosopher Edmund Husserl, who was primarily interested in the systematic study of the structures of consciousness in relation to phenomena. Its methodology involvesto the extent possiblethe setting aside of unexamined preconceptions and presuppositions and experiencing objects and phenomena in a way that is direct and unmediated. Phenomenological methodologies have been adopted by many fields that undertake qualitative research, such as anthropology, psychology, and performanceamong others. These methods often include the systematic observation and description of pre-theoretical experience.",
      "image": null,
      "type": "Module",
      "url": null
    },
